@import '../../../styles/variable.scss';

.modal {
	display: flex;
	align-items: center;
	justify-content: center;

	&__content {
		min-height: 40%;
		min-width: 35%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $color-background;
		border-radius: 1rem;
	}
}
